<template>
  <div class="label-wrap">
    <forum-new-header
      :labels="labels"
    ></forum-new-header>
    <div class="label-main">
      <div class="btns">
        <a href="javascript:void(0)" v-on:touchend="forumBtn(1)">浏览次数</a>
        <a href="javascript:void(0)" v-on:touchend="forumBtn(2)">回复数量</a>
        <a href="javascript:void(0)" v-on:touchend="forumBtn(3)">发布时间</a>
        <a href="javascript:void(0)" v-on:touchend="forumBtn(4)" v-show="forumInfo.moderator">待审核</a>
        <router-link to="/forum/create/new">发表新帖</router-link>
      </div>
      <div class="main">
        <label-posts
          v-for="(item, index) in posts"
          :key="index"
          :posts="item"
        ></label-posts>
        <div class="loadmore" v-show="load">
          <mt-button plain @click="loadMore">加载更多</mt-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ForumHeader from '../forum-header.vue'
import ForumNewHeader from '../forum-new-header.vue'
import LabelPosts from './label-posts.vue'
import {getForumPosts, forumHeaderBtn} from '../../../api/forum'
import {getLocalStorage} from '../../../lib/util'
import {Toast} from 'mint-ui'
export default {
  name: 'Forum-Label',
  data () {
    return {
      labels: [],
      posts: [],
      forumInfo: '',
      load: false,
      page: 1
    }
  },
  watch: {
    '$route' (to, form) {
      this.getDatas()
    }
  },
  created () {
    this.getLabels()
    this.getDatas()
  },
  methods: {
    getDatas () {
      this.$indicator.open({
        text: '加载中...'
      })
      getForumPosts(this.$route.params.id, undefined)
        .then(data => {
          if (data.state) {
            if (data.data.list.length > 20) {
              this.load = true
            }
            this.$indicator.close()
            this.$set(this, 'forumInfo', data.data.info)
            this.$set(this, 'posts', data.data.list)
          }
        })
    },
    getLabels () {
      this.$set(this, 'labels', JSON.parse(getLocalStorage('labels')))
    },
    forumBtn (v) {
      if (v === 1) {
        forumHeaderBtn('browse', this.$route.params.id)
          .then(data => {
            if (data.state) {
              this.$set(this, 'posts', data.data.list)
            } else {
              Toast(data.errormsg || '获取数据失败')
            }
          }).catch(err => console.log(err))
      } else if (v === 2) {
        forumHeaderBtn('reply_amount', this.$route.params.id)
          .then(data => {
            if (data.state) {
              this.$set(this, 'posts', data.data.list)
            } else {
              Toast(data.errormsg || '获取数据失败')
            }
          }).catch(err => console.log(err))
      } else if (v === 3) {
        forumHeaderBtn('create_time', this.$route.params.id)
          .then(data => {
            if (data.state) {
              this.$set(this, 'posts', data.data.list)
            } else {
              Toast(data.errormsg || '获取数据失败')
            }
          }).catch(err => console.log(err))
      } else if (v === 4) {
        forumHeaderBtn('apply', this.$route.params.id)
          .then(data => {
            if (data.state) {
              this.$set(this, 'posts', data.data.list)
            } else {
              Toast(data.errormsg || '获取数据失败')
            }
          }).catch(err => console.log(err))
      }
    },
    loadMore () {
      this.$indicator.open({
        text: '加载中...'
      })
      this.page = this.page + 1
      getForumPosts(this.$route.params.id, this.page)
        .then(data => {
          if (data.state) {
            this.$indicator.close()
            if (data.data.list.length === 0 || data.data.list.length < 20) {
              this.load = false
            }
            this.posts.push(...data.data.list)
          } else {
            this.$indicator.close()
          }
        })
    }
  },
  components: {
    ForumHeader,
    ForumNewHeader,
    LabelPosts
  }
}
</script>

<style lang="less" scoped>
  .label-wrap{
    height: 100%;
    background-color: #e5e5e5;
    .label-main{
      width: 100%;
      position: absolute;
      padding-top: 50px;
      overflow: hidden;
      background-color: #e5e5e5;
      &:after{
        content: " ";
        height: 5rem;
        display: block;
      }
      .main {
        .loadmore{
          text-align: center;
        }
      }
      .btns{
        display: flex;
        padding: .6rem;
        background-color: #f9f9f9;
        a {
          text-align: center;
          flex: 1;
          font-size: 12px;
          color: #222527;
        }
      }
    }
  }
</style>



