<template>
  <div class="posts">
    <div class="img">
      <router-link :to="'/forum/home/' + posts.user_id">
        <img :src="posts.headimgurl" alt="">
      </router-link>
    </div>
    <div class="cont">
      <div class="title">
        <router-link :to="'/forum/posts/' + posts.forum_id">{{posts.title}}</router-link>
      </div>
      <div class="info">
        <router-link :to="'/forum/home/' + posts.user_id">{{posts.user_name}}</router-link>
        <span>{{posts.browse}}次浏览</span>
      </div>
    </div>
    <div class="count">
      <mt-badge type="success" size="small">{{posts.reply_amount}}</mt-badge>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Label-Posts',
  props: {
    posts: Object
  }
}
</script>

<style lang="less" scoped>
  .posts{
    min-height: 68px;
    border-bottom: 1px solid #F0F0F0;
    padding: .6rem .7rem;
    display: flex;
    vertical-align: top;
    background-color: #fff;
    .img{
      width: 3rem;
      height:3rem;
      align-self: center;
      img{
        width: 100%;
        height:100%;
        border-radius: 50%;
      }
    }
    .cont{
      flex: 1;
      padding-left: .6rem;
      .title{
        font-size: 15px;
        margin-bottom: 0;
        a{
          color: #222;
          font-weight: 400;
          line-height: 30px;
        }
      }
      .info{
        color: #ABA8A6;
        font-size: 12px;
        margin-top: 0;
        a{
          color: #ABA8A6;
        }
      }
    }
    .count{
      width: 2rem;
      position: relative;
      span{
        position: absolute;
        top: 50%;
      }
    }
  }
</style>



